<template>
  <div>
    <div v-if="fpid">{{ fpid }}</div>
    <!-- <br />
    <div v-if="filterFpc !== undefined && filterFpc[0] !== undefined">
      {{ JSON.stringify(filterFpc) }}
    </div> -->
    <br />
    <div v-if="fpc !== undefined && fpc[0] !== undefined">
      {{ JSON.stringify(fpc) }}
    </div>
  </div>
</template>

<script>
import FingerprintUtil from "@/module/FingerprintUtil";

export default {
  name: "Fingerprint",
  data() {
    return {
      fpid: "",
      fpc: [],
      result: [],
    };
  },
  created() {},
  mounted() {
    if (window.requestIdleCallback) {
      requestIdleCallback(() => {
        this.doPrompt();
      });
    } else {
      setTimeout(() => {
        this.doPrompt();
      }, 500);
    }
  },
  methods: {
    doPrompt() {
      Fingerprint2.get((components) => {
        this.fpc = components.filter((item) => {
          return !["canvas", "webgl"].some((k) => k === item.key);
        });
        this.toAppScript(JSON.stringify(this.fpc));
        const values = this.filterFpc.map((component) => {
          return component.value;
        });
        this.fpid = Fingerprint2.x64hash128(values.join(""), 31);
      });
    },
    toAppScript(content) {
      const platform = FingerprintUtil.platform();
      if (platform === "ios") {
        if (window.webkit) {
          window.webkit.messageHandler.getAppLink.postMessage(content);
        }
      }
      if (platform === "android") {
        if (window.Android) {
          window.Android.getAppLink(content);
        }
      }
    },
    mapping(component, target) {
      const result = [];
      component.forEach((item) => {
        const tItem = target.find((ti) => {
          return ti.key === item.key;
        });
        if (tItem && tItem.value === item.value) {
          result.push(item);
        }
      });
      this.result = result;
    },
  },
  computed: {
    filterFpc() {
      const allowKeys = [
        "webdriver",
        "language",
        "hardwareConcurrency",
        "timezoneOffset",
        "timezone",
        "sessionStorage",
        "localStorage",
        "indexedDb",
        "addBehavior",
        "cpuClass",
        "platform",
        "hasLiedLanguages",
        "hasLiedResolution",
        "hasLiedOs",
        "hasLiedBrowser",
      ];
      return this.fpc.filter((item) => {
        return allowKeys.some((k) => k === item.key);
      });
    },
  },
};
</script>

